/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  background-color: #08071b;
  color: #ffffff;
}
body::-webkit-scrollbar {
  background-color: #08071b;
  width: 0.8rem;
}
body::-webkit-scrollbar-thumb {
  border-radius: 1rem;
  background-color: #1a1b1c;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.2rem;
}

h3 {
  font-size: 1.8rem;
  text-align: center;
}

p {
  margin: 0;
  font-size: 1.6rem;
}

.container-s {
  width: 95%;
  max-width: 500px;
  margin: 0 auto;
}

.container-m {
  width: 95%;
  max-width: 800px;
  margin: 0 auto;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.app-layout {
  position: fixed;
  top: 1rem;
  left: 0;
  right: 0;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .app-layout {
    top: 50%;
    transform: translateY(-50%);
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.app-layout .crypto-img {
  max-width: 40rem;
  width: 90%;
  margin: 0 auto;
}
.app-layout .app-content {
  grid-row: 2/3;
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
@media (min-width: 768px) {
  .app-layout .app-content {
    grid-row: 1/2;
    grid-column: 2/3;
  }
}
.app-layout .app-content .content-title::after {
  content: "";
  width: 20rem;
  height: 0.3rem;
  background-color: #00e0b6;
  display: block;
  margin: 10px auto 0 auto;
}

.formulary {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
.formulary__button {
  border: 2px solid #04bfe6;
  border-radius: 5rem;
  color: #ffffff;
  font-size: 2rem;
  font-weight: 400;
  padding: 0.8rem;
  width: 100%;
  max-width: 20rem;
  background: transparent;
  transition: 0.2s;
}
.formulary__button:hover {
  color: #1a1b1c;
  background-color: #04bfe6;
  box-shadow: 0 0 10px 3px rgba(4, 191, 230, 0.2);
}
.formulary__label {
  width: 100%;
  text-align: left;
  color: #da35ff;
}
.formulary__select {
  border-radius: 5rem;
  color: #1a1b1c;
  font-size: 2rem;
  font-weight: 400;
  padding: 0.8rem;
  width: 100%;
  border: none;
  border-radius: 5rem;
  font-size: 1.8rem;
  background-color: #00e0b6;
  transition: 0.2s;
  text-align: center;
}
.formulary__select:hover {
  box-shadow: 0 0 10px 1px rgba(0, 224, 182, 0.3);
}