@use "../base/variables" as v;
@use "../base/mixins" as m;

.app-layout {
  // position
  position: fixed;
  top: 1rem;
  left: 0;
  right: 0;
  // structure
  @include m.grid(1, 2rem);

  @include m.min_tablet {
    // position
    top: 50%;
    transform: translateY(-50%);

    // structure
    @include m.grid(2, 2rem);
  }

  .crypto-img {
    max-width: 40rem;
    width: 90%;
    margin: 0 auto;
  }

  .app-content {
    grid-row: 2 / 3;
    display: flex;
    flex-direction: column;
    gap: 4rem;

    @include m.min_tablet {
      grid-row: 1 / 2;
      grid-column: 2 / 3;
    }

    .content-title {
      &::after {
        content: "";
        width: 20rem;
        height: 0.3rem;
        background-color: v.$green;
        display: block;
        margin: 10px auto 0 auto;
      }
    }
  }
}
